.colorpicker-saturation {
  width: 100px;
  height: 100px;
  background-image: url("../images/image13.png");
  cursor: crosshair;
  float: left; }
  .colorpicker-saturation .colorpicker-guide {
    display: block;
    height: 5px;
    width: 5px;
    border: 1px solid #000;
    border-radius: 5px;
    position: absolute;
    top: 0;
    left: 0;
    margin: -4px 0 0 -4px; }
    .colorpicker-saturation .colorpicker-guide i {
      display: block;
      height: 5px;
      width: 5px;
      border: 1px solid #fff;
      border-radius: 5px; }

.colorpicker-hue,
.colorpicker-alpha {
  width: 15px;
  height: 100px;
  float: left;
  cursor: row-resize;
  margin-left: 4px;
  margin-bottom: 4px; }

.colorpicker-hue .colorpicker-guide,
.colorpicker-alpha .colorpicker-guide {
  display: block;
  height: 1px;
  background: #000;
  border-top: 1px solid #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  margin-top: -1px; }

.colorpicker-hue {
  background-image: url("../images/image14.png"); }

.colorpicker-alpha {
  background-image: url("../images/image15.png");
  background-position: 0 100%; }

.colorpicker-preview div {
  position: relative;
  width: 100%;
  height: 100%; }

.colorpicker-swatch {
  cursor: pointer;
  float: left;
  height: 12px;
  width: 12px; }

.colorpicker-swatch + .colorpicker-swatch {
  margin-left: 2px; }

